import './wasm_exec.js';
import './wasmTypes.d.ts';
import './LoadWasm.css';

import React, { useEffect } from 'react';

async function loadWasm(route: String): Promise<void> {
  const goWasm = new (global as any).Go();
  const result = await fetch(`${process.env.PUBLIC_URL}/tools/${route}/main.wasm`).then(response =>
    response.arrayBuffer()
  ).then(bytes =>
    WebAssembly.instantiate(bytes, goWasm.importObject)
  )
  // const result = await WebAssembly.instantiateStreaming(fetch(`${process.env.PUBLIC_URL}/tools${route}/main.wasm`), goWasm.importObject);
  goWasm.run(result.instance);
}

export const LoadWasm: React.FC<React.PropsWithChildren<{ route: String }>> = (props) => {
  const [isLoading, setIsLoading] = React.useState(true);

  useEffect(() => {
    loadWasm(props.route).then(() => {
      setIsLoading(false);
    });
  }, []);

  if (isLoading) {
    return (
      <div className='LoadWasm'>
        loading WebAssembly...
      </div>
    );
  } else {
    return <React.Fragment>{props.children}</React.Fragment>;
  }
};
