import { FC } from 'react';
import React, { Suspense } from 'react';
import { LoadWasm } from '../LoadWasm';

type ToolProps = {
    title: string;
    description: string;
    needWasm: boolean;
    folder: string;
    component: FC;
}
const Tools: ToolProps[] = [
    // {
    //     title: 'Plus',
    //     description: 'Sample code',
    //     folder: 'Plus',
    //     needWasm: true,
    //     component: () => <></>
    // },
    {
        title: 'Base64 encode/decode',
        description: 'Base64 encode or decode a string.',
        folder: 'Base64',
        needWasm: true,
        component: () => <></>
    },
    {
        title: 'JSON formatter',
        description: 'Format JSON.',
        folder: 'Json',
        needWasm: true,
        component: () => <></>
    },
    {
        title: 'JSON/YAML converter',
        description: 'Convert between Json and Yaml.',
        folder: 'Json2Yaml',
        needWasm: true,
        component: () => <></>
    },
    {
        title: 'My IP',
        description: 'Find My IP',
        folder: 'myip',
        needWasm: false,
        component: () => <></>
    }, 
    {
        title: 'Unix Timestamp Converter',
        description: 'Unix Timestamp to date',
        folder: 'timestamp',
        needWasm: true,
        component: () => <></>
    },
    {
        title: 'QR Code Generator',
        description: 'Generate QR Code',
        folder: 'QRcode',
        needWasm: true,
        component: () => <></>
    },
    {
        title: 'QR Code Reader',
        description: 'Read QR Code',
        folder: 'QRcodeReader',
        needWasm: true,
        component: () => <></>
    },
];

for (const tool of Tools) {
    const LazyLoadedComponent = React.lazy(() => import(`../tools/${tool.folder}`));
    if (tool.needWasm) {
        tool.component = () => (
            <LoadWasm route={tool.folder}>
                <Suspense fallback={<div>Loading...</div>}>
                    <LazyLoadedComponent />
                </Suspense>
            </LoadWasm>
        )
    } else {
        tool.component = () => (
            <Suspense fallback={<div>Loading...</div>}>
                <LazyLoadedComponent />
            </Suspense>
        )
    }
}
export { Tools }